import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom';
import { MdMenu } from 'react-icons/md';
import { BsChevronDown } from 'react-icons/bs';
import Collapsible from 'react-collapsible';
import OutsideClickHandler from 'react-outside-click-handler';

import Box from '../../components/Box';
import Button from '../../components/Button';
import { responsive } from '../../components/ThemeProvider/theme';

const menu = [
  {
    label: '關於山模山樣',
    to: '/about',
  },
  {
    label: '登山主題特輯',
    Comp: Collapsible,
    children: (setOpen, history) => topics.map(({ topic, to }, i) => (
        <Box key={i}>
          <Button.Transparent
            as={Link}
            to={to}
            onClick={() => {
              setOpen(false)
            }}
            color="gray"
          >{topic}</Button.Transparent>
        </Box>
      )
    )
  },
  {
    label: '訂閱我們',
    to: true,
  },
]

const topics = [
  // {
  //   topic: '裝備準備',
  //   to: ''
  // },
  // {
  //   topic: '登山組隊',
  //   to: ''
  // },
  // {
  //   topic: '山友責任',
  //   to: ''
  // },
  {
    topic: '無痕山林',
    to: 'lnt'
  }
]


const Menu = ({ openModal, openList }) => {
  const [open, setOpen] = useState()
  const history = useHistory()
  return (
    <>
      <OutsideClickHandler disabled={!Boolean(open)} onOutsideClick={() => setOpen(false)}>
        <Box px={responsive('0.5em', '1em')}>
          <Button.Transparent
            color="lightGray"
            onClick={() => {
              setOpen(!open)
            }}
          >
            <MdMenu size="2.25em" />
          </Button.Transparent>
        </Box>
        <Box
          top="3.5em"
          height="100vh"
          left="0"
          bg="white"
          py="3em"
          px="5em"
          position="absolute"
          transform={open ? 'translateX(0%)' : 'translateX(-100%)'}
          transition="all 0.5s"
          zIndex={99}
        >
          {menu.map(({ label, to, Comp = Button.Transparent, children }, i, { length }) => (
            <Comp
              trigger={
                <Box position="relative" cursor="pointer" mb="0.875em">
                  {label}
                  <Box position="absolute" top="0" right="105%">
                    <BsChevronDown />
                  </Box>
                </Box>
              }
              as={Link}
              to={to}
              onClick={() => {
                (i === length - 1) && openModal()
                setTimeout(() => {
                  setOpen(!open)
                }, 500)
                openList(false)
              }}
              transitionTime={200}
              mb="0.875em"
              key={i}
            >{to ? label : children(setOpen, history)}
            </Comp>
          ))}
        </Box>
      </OutsideClickHandler>
    </>
  )
}

export default Menu
