import React from 'react'
import { FaHeart } from 'react-icons/fa';

import Container from '../components/Container'
import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'

const purpose = [
  '提供一些靈感，下次大家描述山的時候可以試試看，讓每一座山的模樣更鮮明！',
  '如果哪座山勾起你的好奇，下次在床上爬完枕頭山後，就出門去走山吧！',
  '蒐集更多對山的愛與想像，每一次走進山裡，我們都得到太多，所以也很想知道，你得到什麼了呢？'
]

const AboutPage = () => (
  <Container py="2em" maxWidth="800px">
    <Text.H1 textAlign="center">山模山樣</Text.H1>
    <Text.H3 textAlign="center">「除了高度，你能夠用幾種方式來描述一座山的模樣呢？」</Text.H3>
    <Text.Article >
      臺灣是世界上高山密度最高的國家，<br />
      我們有百岳、還有其他大大小小的山，共超過250座，你爬過幾座呢？
    </Text.Article>
    <Text.Article>
      當我們運用想像，嘗試改變和山的互動關係，描述的可能性也愈來愈多！<br />
      感謝許多人參與這個計畫，讓愈來愈多山的個性被看見、被呈現。
    </Text.Article>
    <Text.Article>這個小小的計畫，只有幾個小小的目的：</Text.Article>
    <Box as="ul" my="1em">
      {purpose.map((text, i) => (
        <li key={i}>{text}</li>
      ))}
    </Box>
    <Text.Article>
      -<br />
      Re-lab 團隊
    </Text.Article>
    <Flex alignItems="flex-end">
      <Box mr="0.5em">最開心的是，在今年和大家一起爬山</Box>
      <Box color="red">
        <FaHeart size="2em" />
      </Box>
    </Flex>
    <Box pt="2em">
      <Text.H3 textAlign="center">橘子關懷基金會「敢IN去冒險」</Text.H3>
      <Text.H3 textAlign="center">「2020 Summer School夏日學園」和台灣青年「一起向山致敬」</Text.H3>
      <Text.H3 textAlign="center">用行動號召台灣青年，共創後疫情時代的冒險可能</Text.H3>
      <Text.Article>
        橘子關懷基金會自2008年成立以來，透過各項冒險計畫鼓勵青年挑戰自己，喚起屬於這個世代的冒險精神。今年攜手「台灣外展教育發展基金會」、「米亞桑戶外中心」及「Re-lab」，分別以「體感冒險」與「腦內冒險」為主軸，循序漸進的重啟台灣青年對於冒險的想像，徹底喚醒潛藏在每位台灣青年血液中的冒險基因。
      </Text.Article>
      <Text.Article>
        台灣四面臨海、溪河湍急，更為全世界高山密度最高的島嶼之一。政府於2019年底宣布新山林政策，期待以開放簡化的方式鼓勵大眾走入山林；而隨肺炎疫情改變的新生活模式，人與人之間更應珍視每一次實際互動的機會。同時也需沉靜面對、深層審視大自然給予我們的課題。
      </Text.Article>
      <Text.Article>
        2020年是基金會號召臺灣青年冒險的第13個年頭，決定將臺灣作為錨定，深掘家鄉的文化史脈，從認識腳下土地開始，擁抱台灣根基，順應冒險號召，一同敢IN去冒險。「腦內冒險—轉譯台灣山林，52座群山之最」以臺灣山岳為題，探尋我們與原生環境的關係，用文化與創意磨練青年的思考極限，打破百岳迷思，轉譯屬於新世代的山模山樣。19位學員耗費18小時，完成36座台灣山林資訊圖表，更自願加入山海圳資訊圖表製作，挑戰3小時內從零開始極限腦力激盪。
      </Text.Article>
      <Text.Article>
        登山大多是孤寂的，卻是臺灣山林教給每一位冒險者最深層的課程，冒險也是。臺灣山林更需時刻關注，審視環境讓永續在青年未來的藍圖中發酵。山的模樣也可能是未來的模樣。
      </Text.Article>
      <Text.Article>
        橘子關懷基金會將持續號召台灣青年持續參與屬於台灣的下一場冒險，讓冒險成為一種生活風格、更是每次人類進化的能量。
      </Text.Article>
      <Text.H3 textAlign="center">關於橘子關懷基金會</Text.H3>
      <Text.Article>橘子集團是一個夢想的集合體，匯聚了創辦人Albert、橘子人們的夢想，更乘載了許多社會大眾的夢想。在成立十幾年後、不斷地接受許多外界的支持、幫助的橘子集團，開始反思、期許自己能夠有更集中資源、組織性的付出—善盡「企業社會責任」。於是，2008年1月4日，橘子集團正式啟動「橘子關懷基金會」。</Text.Article>
      <Text.H3 textAlign="center">Summer School工作坊同學名單</Text.H3>
      <Text.Article>感謝以下參與Summer School腦內冒險的同學，在工作坊期間負責查詢各座山的相關資訊，協力完成各座山的資訊模板。</Text.Article>
      <Text.Article>陳俞安、黃亮瑄、黃舒玟、方又芊、黃郁雯、謝沛馨、羅品蓁、陳念廷、柯柔因、蕭筑元、黃意晴、林貫佑、洪薇檸、盛子芸、楊彥彬、廖韋婷、許意𦬕、張雅晴、陳肯</Text.Article>
    </Box>
  </Container>
)

export default AboutPage
